export default {
	data() {
		return {
			graceSkeleton : 'ing',
			//文章对象格式
			//{
			//	title : "标题",
			//	authorFcae : "作者头像",
			//	authorName : "作者姓名",
			//	viewNumber : "浏览次数",
			//	date       : "日期",
			//	contents   : [
			//		{type : "text", content : "文本内容"},
			//		{type : "img", content : "图片路径"},
			//		//.....
			//	]
			//}
			article : {
				contents : []
			}
		}
	},
	onLoad() {
		_self = this;
		// 加载文章详情
		uni.showLoading({});
		uni.request({
			url: 'https://www.easy-mock.com/mock/5bb833775df5622d84ac87ca/example/artinfo_skeleton',
			method: 'GET',
			data: {},
			success: res => {
				// 此处先规划骨架
				var art = {contents : []};
				for(var i = 0; i < res.data.contents.length; i++){
					art.contents.push({'type': res.data.contents[i].type});
				}
				_self.article =  art;
				// 骨架屏规划后延长 500 毫秒进行数据替换
				setTimeout(function(){
					_self.article = res.data;
					_self.graceSkeleton = 'end';
				}, 500);
			},
			fail: () => {},
			complete: () => {
				uni.hideLoading();
			}
		});
	},
	methods: {

	}
}
